.footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

.footer-logo{
    display: flex;
    align-items: center;
    gap: 20px;
}

.footer-logo img{
    width: 57px;
    height: 58px
}

.footer-logo p{
    color: #383838;
    font-size: 40px;
    font-weight: 700;
}

.footer-links{
    display: flex;
    list-style: none;
    gap: 50px;
    color: #252525;
    font-size: 20px;
}

.footer-links li{
    cursor: pointer;
}

.footer-social-icon{
    display: flex;
    gap: 20px;
    align-items: center;
}

.footer-icon-container img{
    width: 57px;
    height: 58px;
    align-items: center;
    justify-content: center;
}

.nav-logo img{
    width: 57px;
    height: 58px;
    align-items: center;
}

.footer-icon-container{
    padding: 10px;
    padding-bottom:6px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
}

.footer-copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-top: 30px;
    color: #1a1a1a;
    font-size: 20px;

}

.footer-copyright hr{
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}

@media(max-width:800px){
    .footer{
        gap: 20px;

    }
    .footer-logo{
        grid-area: 10px;
        align-items: end;

    }
    .footer-logo img{
        width: 40px;

    }
    .footer-logo p{
        font-size: 25px;

    }
    .footer-links{
        font-size: 14px;
        grid-area: 10px;


    }
    .footer-social-icon img{
        width: 42px;
        height: 40px;
    }
    .footer-copyright{
        font-size: 13px;
    }
}

@media(max-width:500px){
    .footer-links{
        font-size: 12px;
        /* grid-area: 2px; */
    }
    .footer-copyright{
        font-size: 12px;
        text-align: center;
        justify-content: center;
    }
}

@media(max-width:410px){
    .footer-links{
        font-size: 8px;
        grid-area: 2px;
        font-weight: 600;
    }
    .footer-copyright{
        font-size: 12px;
        text-align: center;
        justify-content: center;
    }
}
